<template>
  <div class="flex-box" style="height: 100%">
    <el-card class="box-card crd flex-box" style="height: auto">
      <el-form>
        <el-form-item label="当前手机号">
          <el-input
            v-model="phoneInfo.phone"
            placeholder="绑定手机号或更换手机号"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <div class="flex-box" style="justify-content: space-around">
            图片验证码：<el-input
              style="width: 50%"
              placeholder="图片验证码"
              v-model="phoneInfo.imageCode"
            ></el-input>
            <div @click="createImg" class="flex-box">
              <img :src="img" />
            </div>
          </div>
        </el-form-item>
        <el-form-item class="flex-box">
          <el-button type="success" @click="sendPhone">发送验证码</el-button>
        </el-form-item>
        <el-form-item>
          <div class="flex-box" style="justify-content: space-around">
            手机验证码：<el-input
              style="width: 50%"
              placeholder="手机验证码"
              :disabled="phoneDisabled"
              v-model="phoneInfo.code"
            ></el-input>
          </div>
        </el-form-item>
        <el-form-item class="flex-box">
          <el-button type="primary" @click="updatePhone">确定修改</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import tools from '@/js/tools';
export default {
  data() {
    return {
      phoneDisabled: true,
      phonePopover: false,
      phoneInfo: {
        code: '',
        phone: '',
        imageCode: ''
      },
      img: ''
    };
  },
  methods: {
    createImg() {
      tools.NoAjax('/tool/getImageCode', {}, (data) => {
        this.img = data.message;
      });
    },
    sendPhone() {
      delete this.phoneInfo.code;
      tools.NoAjax(
        '/tool/sendValidateCode',
        this.phoneInfo,
        (data) => {
          if (data.success) {
            this.phoneDisabled = false;
            this.phonePopover = true;
            this.$message(data.message);
          } else {
            if (data.message == '验证码长期有效，无需重复发送') {
              this.phoneDisabled = false;
              this.phonePopover = true;
            } else {
              this.phoneInfo.imgCode = '';
              this.createImg();
            }
            this.$message(data.message);
          }
        },
        true
      );
    },
    updatePhone() {
      tools.NoAjax('/user/auth/updateUserPhone', this.phoneInfo, (data) => {
        this.$message(data.message);
        this.$store.dispatch('updatedUser');
        this.phoneInfo.imgCode = '';
        this.createImg();
        this.phoneInfo = {
          code: '',
          phone: '',
          imageCode: ''
        };
      });
    }
  },
  created() {
    this.createImg();
    this.phoneInfo.phone = this.$store.state.user.tbUserInfo.phone;
  }
};
</script>

<style scoped>
.crd {
  padding: 2rem;
}
</style>
